#intro {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 15%;
    padding-top: 20%;
    min-height: 70vh;
}

.intro-subtitle {
    margin-top: -5vh;
    color: var(--grey);
    font-weight: 600;
    font-size: 36px;
    width: 55%;
}

.intro-desc {
    margin-top: 0.5vh;
    color: var(--grey);
    width: 50%;
    font-size: 22px;
    padding-bottom: 4vh;
}

.intro-contact {
    font-size: 20px;
    font-weight: bolder;
    border: 2px solid var(--light-green);
    padding: 16px;
    cursor: pointer;
    border-radius: 8px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.intro-contact-blurb {
    color: var(--dark-green);
    padding-left: 5px;
}

a.intro-contact:hover {
    color: var(--grey);
    background-color: var(--beige);
}

.MuiSvgIcon-root.MuiSvgIcon-fontSizeMedium.css-i4bv87-MuiSvgIcon-root {
    margin-right: 5px;
}

/* on 768px OR SMALLER displays - TABLET */
@media only screen and (max-width: 826px) {
    #intro {
        min-height: 42vh;
    }

    .intro-subtitle {
        margin-top: -2vh;
        font-size: 28px;
        width: 80%;
    }

    .intro-desc {
        width: 80%;
        font-size: 20px;
        padding-bottom: 3vh;
    }
}

/* on 576px OR SMALLER displays - PHONE  */
@media only screen and (max-width: 576px) {
    #intro {
        padding-left: 5%;
        min-height: 62vh;
    }

    .intro-subtitle {
        font-size: 16px;
    }

    .intro-desc {
        font-size: 14px;
        padding-top: 8vh;
        padding-bottom: 5vh;
    }

    .intro-contact {
        font-size: 16px;
    }
}